import Home from './componets/home.vue'
import Login from './componets/login.vue'
import Regist from './componets/Regist.vue'

const routes = [{
	path: '/home',
	component: Home
},{
	path: '/login',
	component: Login
},{
	path: '/',
	redirect: '/home'
},{
	path: '/regist',
	component: Regist
}];

export default routes


