<template>
  <section class="msgbox">
    <div class="error-box" v-if="error">
      <span>无法连接到网络……</span>
    </div>
    <div class="loading-box" v-if="loading">
      <span>loading……</span>
    </div>
    <ul>
      <li v-for="(item,index) in List">
        <a href="javascript:;">{{item.content | trim}}</a>
      </li>
    </ul>
  </section>
</template>


<script>
  export default{
    data(){
      return {
        List:[],
        loading:true,
        error:false
      }
    },created(){
      this.fetchData();
    },methods:{
      fetchData(){
        this.$http.jsonp('https://bird.ioliu.cn/joke/rand',{
        params:{
          type:'text'
        },
        jsonp:'callback'
      }).then(res=>{
        this.List=res.data.data;
        this.error=false;
        this.loading=false;
      },err=>{
        this.error=true;
        this.loading=false;
      })
      }
    }
  }
</script>

<style scoped> 
  .error-box{
    text-align: center;
  }
  .loading-box{
    text-align: center;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>


